import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IndexService } from 'src/app/Shared/index.service';

@Component({
  selector: 'app-subjects',
  templateUrl: './subjects.page.html',
  styleUrls: ['./subjects.page.scss'],
})
export class SubjectsPage implements OnInit {
  data: any;
  constructor(
    private indexser: IndexService,
    private activatedRoute: ActivatedRoute
  ) {}
  hide: boolean;
  id: any;
  coursearr: any;
  datagot: boolean;

  ngOnInit() {
    this.hide = false;
    this.datagot = false;
    this.id = this.activatedRoute.snapshot.paramMap.get('id');
    this.indexser.getSingleCourse(this.id).subscribe(
      (res: any) => {
        console.log(res);
        this.coursearr = res.record[0].subjects;
        this.data = {
          name: this.coursearr[0].subjectName,
          subId: this.coursearr[0]._id,
          courseid: this.coursearr[0].courseId,
        };
        this.indexser.subject.next(this.data);
        console.log(this.data);
        this.datagot = true;
      },
      (err) => {
        console.log(err);
      }
    );
  }

  openNav() {
    this.hide = false;
    document.getElementById('mySidenav').style.width = '200px';
    document.getElementById('main').style.marginLeft = '200px';
  }

  closeNav() {
    this.hide = true;
    document.getElementById('mySidenav').style.width = '0px';
    document.getElementById('main').style.marginLeft = '0';
  }

  getflash(subjname: string, id: string, cid: string) {
    this.data = {
      name: subjname,
      subId: id,
      courseid: cid,
    };
    this.indexser.subject.next(this.data);
  }
}
